import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Acao_corretiva } from '../../../modelo/Acao_corretiva'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'acao_corretiva-dados-edit',   templateUrl: 'acao_corretiva_dadosEdit.component.html' }) export class Acao_corretivaDadosEditComponent implements OnInit {    acao_corretiva: Acao_corretiva;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.acao_corretiva.Acao_corretiva";                 let id = +params['id'];         this.acao_corretiva = new Acao_corretiva();         this.acao_corretiva.cd_acao_corretiva = id;         o.objetoJson = this.acao_corretiva;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a acao_corretiva. ';       }      this.acao_corretiva = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


